import React, { Component } from 'react';
import { Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import CheckoutSummary from '../../components/Order/CheckoutSummary/CheckoutSummary';
import ContactData from './ContactData/ContactData';


class Checkout extends Component {    

    checkoutCanceledHandler = () => {
        this.props.history.goBack();
    }

    checkoutContinuedHandler = () => {
        /* replace changes url */
        this.props.history.replace('/react16_demo_project/checkout/contact-data');
    }

    /* old version with query */
    //componentWillMount() {
    //    // console.log('[Checkout.js] props: ', this.props);
    //    /* read special props location.search and store them in a query variable */
    //    const query = new URLSearchParams(this.props.location.search);        
    //    /* new object that will takes all query props and pass them into state */
    //    const ingredients = {};
    //    let price = 0;
    //    for(let param of query.entries()) {
    //        /* entrie  will looks like this: ['salad', '1'] */
    //        if(param[0] === 'price') {
    //            price = param[1];
    //        } else {
    //            /* store params like key value pairs */
    //            ingredients[param[0]] = +param[1];
    //        }            
    //    }
    //    // console.log('[Checkout.js] ingredients: ', ingredients);
    //    this.setState({ingredients: ingredients, price: price });
    //    
    //}

    render() {
        let summary = <Redirect to='/react16_demo_project/' />
        if (this.props.ings) {
            const purchasedRedirect = this.props.purchased ? <Redirect to='/react16_demo_project/' /> : null;
            summary = (
                <div>
                    {purchasedRedirect}
                    <CheckoutSummary
                        ingredients={this.props.ings}
                        checkoutCanceled={this.checkoutCanceledHandler}
                        checkoutContinued={this.checkoutContinuedHandler} />
                    <Route
                        path={this.props.match.path + '/contact-data'}
                        component={ContactData} />
                </div>
            );
        }
        return (
            <div>
                {summary}                
            </div>
        );
    }
}

const mapStateToProps = state => {
    /*  left side is local state name.
        can be named whatever you want.
        right side is the reducer state name reference */
    return {
        ings: state.burgerBuilder.ingredients,
        purchased: state.order.purchased
    };
}

export default connect(mapStateToProps)(Checkout);


